import Compressor from 'compressorjs';
import caseImage1 from '../asset/case1/case__new.png';
import axios from 'axios';
import { useState } from 'react';


const file_output = '../asset/test';

const ImageProcessTestPage = () => {

    // const handleFileProcessing = async () => {

    //     try {
    //         // 1.  300x300  Base64
    //         const resizedBase64 = await resizeImage(caseImage1, file_output, 0.1, 'png');
    //         console.log("Resized Base64:", resizedBase64);

    //         // 2.  Base64
    //         const base64Data = await encodeBase64(resizedBase64);
    //         console.log("Original Base64:", base64Data);

    //         // 3.  Base64  Blob
    //         const decodedBlob = decodeBase64(base64Data, file_output);
    //         console.log("Decoded Blob:", decodedBlob);
    //         console.log("Image saved successfully.");

    //     } catch (error) {
    //         console.error("Error processing file:", error);
    //     }
    // };

    // 2.  Base64

    const [rawImg, setRawImg] = useState(null); //File
    const [resultImg, setResultImg] = useState(null); // url

    const imagePath = "../asset/case1/case__new.png"; //  public 

    const handleFileChange = (event) => {
        const file = event.target.files[0]; // 
        if (file) {
            setRawImg(file);
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => {
                // setBlobFileContent(reader.result); //  state
            };
            reader.onerror = () => {
                console.error("");
            };
        }
    };


    const handleOnClick = () => {
        async function convertImageToFile() {
            try {
                const response = await fetch(caseImage1); // 
                const blob = await response.blob(); //  Blob 
                const file = new File([blob], "case__new.png", { type: blob.type }); //  File 
                setRawImg(file); // 
                console.log("File object:", file);
            } catch (error) {
                console.error("Error converting image to File:", error);
            }
        }

        convertImageToFile();
    }

    const handleSubmit = () => {
        new Compressor(rawImg, {
            quality: 0.6,
            maxHeight: 1080,
            convertTypes: "image/jpg",
            // The compression process is asynchronous,
            // which means you have to access the `result` in the `success` hook function.
            success(result) {
                const formData = new FormData();

                // The third parameter is required for server
                formData.append('file', result, result.name);
                console.log('Load success', formData);
                // Send the compressed image file to server with XMLHttpRequest.
                // axios.post('/path/to/upload', formData).then(() => {
                //     console.log('Upload success');
                // });

                //  Blob  URL 
                const url = URL.createObjectURL(result);
                setResultImg(url);
            },
            error(err) {
                console.log(err.message);
            },
        });
    }


    return (
        <div>
            <h1></h1>
            <input type="file" onChange={handleFileChange} />
            <div>
                <h3></h3>
                {resultImg && <img alt='test' src={resultImg}></img>}
            </div>
            <button type="button" onClick={handleOnClick}></button>
            <button type='button' onClick={handleSubmit}></button>
        </div>
    )
}

export default ImageProcessTestPage;